	module.exports = {"data":{"site":{"siteMetadata":{"title":"React ðŸŽ„","author":"Michael Chan @chantastic"}},"markdownRemark":{"id":"/Users/chan/learnreact/react.holiday/src/pages/2017/1.md absPath of file >>> MarkdownRemark","html":"<div class=\"measure\">\n<p>In <code>C</code>, programs have a <code>main()</code> function.\nItâ€™s where programs starts.</p>\n<p>React apps are similar; on the web they start with <code>ReactDOM.render()</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code>ReactDOM<span class=\"token punctuation\">.</span><span class=\"token function\">render</span><span class=\"token punctuation\">(</span>someReactComponent<span class=\"token punctuation\">,</span> someDOMNode<span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<h3>WTF is ReactDOM?</h3>\n<p>React is used all overâ€”web, mobile, desktop, VR, appliances.\nLots of places.</p>\n<p>Instead of stuffing all those smarts into one enormous library,\neach platform has a partner library.</p>\n<p>On the web, thatâ€™s <code>ReactDOM</code>.</p>\n<p>We use <code>React</code> to create components and <code>ReactDOM</code> to render them into the DOM.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code>ReactDOM<span class=\"token punctuation\">.</span><span class=\"token function\">render</span><span class=\"token punctuation\">(</span>\n  React<span class=\"token punctuation\">.</span><span class=\"token function\">createElement</span><span class=\"token punctuation\">(</span><span class=\"token string\">'h1'</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Hello ðŸŽ„'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n  document<span class=\"token punctuation\">.</span><span class=\"token function\">getElementById</span><span class=\"token punctuation\">(</span><span class=\"token string\">'root'</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<h2>Tinker with it</h2>\n<p>Use the window below and play around with <code>render()</code>.</p>\n<ul>\n<li>Comment out the import statements. What errors do you get?</li>\n<li>Change the component type to something other than <code>h1</code>.</li>\n<li>Put some values in the empty object (second argument). What happens? What errors do you get?</li>\n</ul>\n</div>\n<iframe src=\"https://codesandbox.io/embed/l396jqvnoz\" style=\"width:100%; height:500px; border:0; border-radius: 4px; overflow:hidden; border: 1px solid black\" sandbox=\"allow-modals allow-forms allow-popups allow-scripts allow-same-origin\"></iframe>","frontmatter":{"title":"Day 1: Hello ðŸŽ„","date":"December 01, 2017"}}},"pathContext":{"slug":"/2017/1/"}}

